import styled from 'styled-components';
import CityMap from '../../../assets/icon/city-map.png';

export const Contacts = styled.main`
  position: relative;
  background-image: url(${CityMap}),
    linear-gradient(360deg, #7e6af3 15.95%, #6540cd 63.1%);
  padding: 77px 80px 95px;
  display: flex;
  column-gap: 34px;
`;
